import React from 'react';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import decorateToolbarChildren from './decorateToolbarChildren';

const blockName = 'toolbar-group';

const ToolbarGroupContainer = aggregatedClasses('div')(
  blockName,
  null,
  ({ space }) => ({
    [`space-${space}`]: !!space,
  }),
);

const ToolbarGroup = ({ children, size, space = 0 }) => (
  <ToolbarGroupContainer classProps={{ space }}>
    {decorateToolbarChildren(children, { size })}
  </ToolbarGroupContainer>
);

export default ToolbarGroup;
